"use client";

import React, { useState } from "react";
import Image from "next/image";
import { Bath, BedDouble, MapPin, Ruler, Search } from "lucide-react";
import FreeAddressSearch from "./FreeAddressSearch";
import { Button } from "@/components/ui/button";
import FilterSection from "./FilterSection";

function Listing({ listing = [], handleSearchClick = [], searchedAddress ,
 setBathCount,
 setBedCount,
 setParkingCount,
 setHomeType,

}) {
  const [address, setAddress] = useState();

  return (
    <div>
      <div className="p-3 flex gap-6">
        <FreeAddressSearch
          selectedAddress={(v) => {
            searchedAddress && searchedAddress(v);
            setAddress(v);
          }}
          setCoordinates={(v) => console.log(v)}
        />

        <Button className="flex gap-2" onClick={handleSearchClick}>
          <Search className="h-4 w-4" /> Search
        </Button>
      </div>

      <FilterSection
      setBathCount={setBathCount}
      setBedCount={setBedCount}
      setParkingCount={setParkingCount}
      setHomeType={setHomeType}
      />

      <div>
        <br />
        {address && listing?.length > 0 && (
          <div className="w-full mt-2 text-sm text-gray-600">
            <h2 className="text-lg">
              Found {listing.length} result{listing.length > 1 ? "s" : ""} in{" "}
              <span className="text-purple-500 font-bold">
                {address?.label || address}
              </span>
            </h2>
          </div>
        )}
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {listing?.length > 0
          ? listing.map((item) => (
              <div
                key={item.id}
                className="p-3 hover:border hover:border-primary cursor-pointer rounded-lg"
              >
                <h2 className="font-bold text-xl text-blue-600">₹{item.price}</h2>

                <div className="flex gap-2 items-center text-sm text-gray-500">
                  <MapPin className="h-4 w-4" />
                  <span>{item?.address}</span>
                </div>

                <div className="flex gap-2 mt-2 justify-between">
                  <h2 className="flex gap-2 text-xs bg-slate-200 rounded-md px-2 py-1">
                    <BedDouble className="h-4 w-4" />
                    {item?.bedroom}
                  </h2>
                  <h2 className="flex gap-2 text-xs bg-slate-200 rounded-md px-2 py-1">
                    <Bath className="h-4 w-4" />
                    {item?.bathroom}
                  </h2>
                  <h2 className="flex gap-2 text-xs bg-slate-200 rounded-md px-2 py-1">
                    <Ruler className="h-4 w-4" />
                    {item?.area}
                  </h2>
                </div>
              </div>
            ))
          : [1,2,3,4,5,6,7,8].map((_, index) => (
              <div
                key={index}
                className="h-[230px] w-full bg-slate-200 animate-pulse rounded-lg"
              />
            ))}
      </div>
    </div>
  );
}

export default Listing;
